export function isPrimitiveType(data) {
  return !isConstructorTypeCode(data.type);
}
export function isColl(data) {
  return data.type >= 0x0c && data.type <= 0x23;
}
export function isEmbeddableTypeCode(typeCode) {
  return typeCode >= 0x01 && typeCode <= 0x0b;
}
export function isPrimitiveTypeCode(typeCode) {
  return !isConstructorTypeCode(typeCode);
}
export function isConstructorTypeCode(type) {
  return type >= 0x0c && type <= 0x60;
}